"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";
import { liveClasses } from "@/data/classes";

export default function LiveClasses() {
  const [selectedClass, setSelectedClass] = useState<typeof liveClasses[0] | null>(null);

  const joinClass = (course: typeof liveClasses[0]) => {
    // In a real application, this would handle the connection to a live streaming service
    window.open(course.meetingUrl, '_blank');
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-4xl font-bold mb-8">Live Classes</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {liveClasses.map((course) => (
          <Card key={course.id} className="overflow-hidden">
            <img
              src={course.thumbnail}
              alt={course.title}
              className="w-full h-48 object-cover"
            />
            <div className="p-4">
              <h3 className="text-xl font-semibold mb-2">{course.title}</h3>
              <p className="text-gray-600 mb-1">Instructor: {course.instructor}</p>
              <p className="text-gray-600 mb-1">Date: {course.date}</p>
              <p className="text-gray-600 mb-3">Time: {course.time}</p>
              <div className="space-y-2">
                <Button 
                  className="w-full"
                  onClick={() => joinClass(course)}
                >
                  Join Live Class
                </Button>
                <Button 
                  variant="outline" 
                  className="w-full"
                  onClick={() => setSelectedClass(course)}
                >
                  View Details
                </Button>
              </div>
            </div>
          </Card>
        ))}
      </div>

      <Dialog open={!!selectedClass} onOpenChange={() => setSelectedClass(null)}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>{selectedClass?.title}</DialogTitle>
            <DialogDescription>
              <div className="space-y-4 mt-4">
                <img
                  src={selectedClass?.thumbnail}
                  alt={selectedClass?.title}
                  className="w-full h-48 object-cover rounded-lg"
                />
                <div>
                  <p className="font-semibold">Instructor</p>
                  <p className="text-gray-600">{selectedClass?.instructor}</p>
                </div>
                <div>
                  <p className="font-semibold">Schedule</p>
                  <p className="text-gray-600">{selectedClass?.date} at {selectedClass?.time}</p>
                  <p className="text-gray-600">Duration: {selectedClass?.duration}</p>
                </div>
                <div>
                  <p className="font-semibold">Description</p>
                  <p className="text-gray-600">{selectedClass?.description}</p>
                </div>
                <Button 
                  className="w-full"
                  onClick={() => selectedClass && joinClass(selectedClass)}
                >
                  Join Live Class
                </Button>
              </div>
            </DialogDescription>
          </DialogHeader>
        </DialogContent>
      </Dialog>
    </div>
  );
}
